@font-face {
    font-family: 'allerregular';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('woff');
    font-weight: normal;
    font-style: normal;
}

* {
    --major-gap: 36px;
    --minor-gap: 24px;
    --major-radius: 18px;
}

body {

    font-family: allerregular;
    width: 100vw;
    height: 100vh;

    .container {
        width: 100%;
        height: 100%;
        padding: var(--major-gap);

        display: grid;
        grid-template: 2fr 1fr / 1fr 1fr;
        justify-items: center;
        align-items: center;
        gap: var(--major-gap);

        .section {
            height: 100%;
            width: 100%;
            min-width: 610px;
            padding: var(--major-gap);
            border-radius: var(--major-radius);
        }

        .section.main {
            padding: 0;

            display: flex;
            flex-direction: column;
            gap: var(--minor-gap);

            border: none;

            form {
                width: 100%;
                height: 50px;

                display: grid;
                grid-template-columns: 1fr 100px;
                gap: var(--minor-gap);

                * {
                    border-radius: 8px;
                }
            }

            .card.main {
                height: 100%;
                width: 100%;
                padding: var(--major-gap);

                display: grid;
                grid-template-columns: 3fr 2fr;
                justify-items: center;
                align-items: center;
                gap: var(--minor-gap);

                border-radius: var(--major-radius);

                p.temp {
                    font-size: 10rem;
                    display: flex;
                    flex-direction: column;

                    span {
                        font-size: 3rem;
                        justify-self: center;
                        align-self: center;
                    }
                }

                .prec-info {
                    width: 100%;

                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    img {
                        width: 100%;
                        height: auto;
                    }

                    p.prec-prob {
                        font-size: 4rem;
                    }

                }

            }
        }

        .section.desc {
            font-size: 2rem;
            text-align: center;
            text-wrap: wrap;

            display: flex;
            align-items: center;
            justify-content: center;
        }

        .section.info {
            font-size: 3rem;
            text-align: center;
            text-wrap: nowrap;

            display: flex;
            align-items: center;
            justify-content: center;

            tr {
                display: grid;
                grid-template-columns: 1fr 1fr;
                gap: var(--minor-gap);

                th {
                    text-align: right;
                }

                td {
                    text-align: left;
                }
            }

        }

        .section.week {
            display: flex;
            justify-content: center;
            align-items: center;
            gap: var(--minor-gap);

            &>div {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                font-weight: bold;
                height: 100%;

                img {
                    height: 50%;
                    width: 100%;
                }
            }
        }

    }

